export default {
  layout: 'product',
  async asyncData (context) {
    const id = context.params.id
    try {
      const product = await context.app.$axios.$get(
        `https://my-json-server.typicode.com/DhandyF/Catalogue/product/${id}`
      )
      return { product }
    } catch (e) {
      context.error(e) // Show the nuxt error page with the thrown error
    }
  },
  data () {
    return {
      slideIndex: 1
    }
  },
  mounted () {
    this.showSlide(this.slideIndex)
  },
  methods: {
    showSlide (n) {
      const slides = document.getElementsByClassName('carousel-item')
      if (n > slides.length) {
        this.slideIndex = 1
      }
      if (n < 1) {
        this.slideIndex = slides.length
      }
      for (let index = 0; index < slides.length; index++) {
        slides[index].style.display = 'none'
      }
      slides[this.slideIndex - 1].style.display = 'flex'
    },
    showImage (slideIndex) {
      this.showSlide(this.slideIndex = slideIndex)
    },
    plusSlide (slideIndex) {
      this.showSlide(this.slideIndex += slideIndex)
    },
    currentSlide (slideIndex) {
      this.slideIndex = slideIndex
      this.showSlide(slideIndex)
    },
    scrollRight () {
      const imgElement = document.getElementById('image-group')
      let scrollAmount = 0
      const slideTimer = setInterval(function () {
        imgElement.scrollLeft += 20
        scrollAmount += 10
        if (scrollAmount >= 100) {
          window.clearInterval(slideTimer)
        }
      }, 25)
    },
    scrollLeft () {
      const imgElement = document.getElementById('image-group')
      let scrollAmount = 0
      const slideTimer = setInterval(function () {
        imgElement.scrollLeft -= 20
        scrollAmount += 10
        if (scrollAmount >= 100) {
          window.clearInterval(slideTimer)
        }
      }, 25)
    }
  }
}
